import React, { useState, useEffect } from "react";
import { getSummary } from "../../services/http";
import { cpFirst } from "../../services/utilities";
import config from "../../config";
import _ from "lodash";
import Nav from "../common/nav";
import Footer from "../common/footer";
import Input from "../common/form-input";
import Select from "../common/form-select";

const Profile = (params) => {
  const [userValues, setUserValues] = useState({
    name: "Test Tester",
    email: "test@test.com",
    phone: "281-281-2865",
    password: "",
    verifyPassword: "",
    defaultDateRange: "",
  });
  const [methodCards, setMethodCards] = useState(null);

  useEffect(() => {
    getSummaryData();
  }, []);

  const getSummaryData = async () => {
    const { data } = await getSummary();
  };

  const updateValues = (e) => {
    let update = userValues;
    switch (e.name) {
      case "name":
        update.name = cpFirst(e.value);
        break;
      case "email":
        update.email = e.value;
        break;
      case "phone":
        update.phone = e.value;
        break;
      case "defaultDateRange":
        update.defaultDateRange = e.value;
        break;
      case "password":
        update.password = e.value;
        break;
      case "verifyPassword":
        update.verifyPassword = e.value;
        break;
      default:
        return;
    }
    setUserValues({ ...userValues, ...update });
  };
  return (
    <>
      <Nav />
      <section>
        <div className="container">
          <div className="mb-4">
            <h1>Profile</h1>
          </div>
          <hr />
          <h5>Personal information</h5>
          <div className="flex-wrapper m-space shadow-box mb-5">
            <Input
              label="Name"
              help="Update you name."
              className=""
              name="name"
              value={userValues.name}
              onChange={(e) => updateValues(e)}
            />
            <Input
              type="email"
              label="Email"
              help="Update you email address."
              className=""
              name="email"
              value={userValues.email}
              onChange={(e) => updateValues(e)}
            />
            <Input
              type="phone"
              label="Phone"
              help={
                userValues.phone
                  ? "Update your phone number."
                  : "Add your phone number for security."
              }
              className="mw-150"
              name="phone"
              value={userValues.phone}
              onChange={(e) => updateValues(e)}
            />
          </div>
          <h5>Envelope preferences</h5>
          <div className="flex-wrapper mb-5">
            <div className="card w-100 mw-450 pt-1 mb-5">
              <div className="me-4 ">
                <ul className="list-group-flush p-0">
                  <li className="list-group-item">
                    <b>Monthly</b> | view transactions by month
                  </li>
                  <li className="list-group-item">
                    <b>Split</b> | view transactions by the 1st and 15th
                  </li>
                  <li className="list-group-item">
                    <b>Bi-Weekly</b> | view transactions by every two weeks
                  </li>
                  <li className="list-group-item">
                    <b>Weekly</b> | view transactions by week
                  </li>
                </ul>
              </div>
            </div>
            <div className="card card-center mb-5 p-2">
              <Select
                label="Default date range"
                help="Update default date range."
                defaultValue={userValues.defaultDateRange}
                onChange={(e) => updateValues(e)}
                className="mw-150"
                name="defaultDepositMethod"
                options={config.date.range}
              />
            </div>
          </div>
          <h5>Password Reset</h5>
          <div className="flex-wrapper m-space shadow-box mb-4">
            <Input
              type="password"
              label="Password"
              help="Update your password."
              className=""
              name="password"
              value={userValues.password}
              onChange={(e) => updateValues(e)}
            />
            {!_.isEmpty(userValues.password) && (
              <Input
                type="password"
                label="Verify password"
                help="Re-enter your password."
                className=""
                name="verifyPassword"
                value={userValues.verifyPassword}
                onChange={(e) => updateValues(e)}
              />
            )}
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};
export default Profile;
